<template>
  <p class="blank"></p>
  <br><br><br>
  <div class="row">
    <div v-if="list">
      <div v-for="i in list" :key="i.id" class="col-xs-6 list">
        <router-link :to="{ name: 'details', params: { id: i.id } }">
          <div class="img col-xs-5" >
            <img :src="this.url + i.cover" alt=""/>
          </div>
          <div class="col-xs-7">
            <h3>{{ i.name }}</h3>
              <h4>{{ i.language }}/{{ i.duration }}分钟</h4>
              <h4 style="color: #ffc600">{{ i.likeNum }}人想看</h4>
              <h4 class="text-muted">{{ i.playDate }}上映</h4>
          </div>
        </router-link>
      </div>
    </div>
    <div v-if="list.length ==0">
      <br><br><br><br><br><br>
      <h3 class="text-center">查无结果</h3>
      <br><br><br><br><br><br><br>
    </div>
  </div>
  <br><br><br>
</template>

<script>
export default {
  // props:{msg:{type:String,default:' '}},
  props:['msg'],
  data(){
    return{
      list:[],
        url:"http://124.93.196.45:10001/",
    }
  },
  created(){
    console.log()
    let msg =this.msg.substring(1,this.msg.length)
    this.axios({
      method:"get",
      url:"/prod-api/api/movie/film/list?name="+msg,
    })
    .then((resopnse)=>{
      if(resopnse.status == 200){
        if(resopnse.data.code == 200){
          console.log(resopnse.data)
          this.list = resopnse.data.rows
          console.log(this.list);
        }else{
          console.log(resopnse.data)
          console.log(this.list);
        }
      }else{
        console.log(resopnse.data)
      }
    })
    .catch((error)=>{
      console.log(error)
    })
  },
}
</script>

<style scoped>
.img img{
  width: 100%;
  height: 240px;
}
.list{
  background: #fff;
  padding: 20px;
  margin: 20px 0px;
}
.list:nth-child(2n){
  border-left: 1px dotted rgb(126, 125, 125);
}
a{
  color: black;
  text-decoration: none;
}
a:hover h3{
  color: #d63830;
}
</style>